.sidebar {
  grid-area: sidebar;
  position: relative;
  background: var(--main-color);
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-start;
  z-index: 99999;
  box-shadow: 0 0 10px #00000010;
  overflow: hidden;
}

.sidebar .sidebar-button {
  position: absolute;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  background: linear-gradient(
    90deg,
    var(--main-color) 0%,
    var(--second-color) 100%
  );
  top: 30px;
  right: 15px;
  width: 20px;
  height: 20px;
  border-radius: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  display: none;
}

.sidebar .sidebar-button svg {
  width: 10px;
  height: 10px;
  fill: #fff;
}

.sidebar ul {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  width: 100%;
}

.sidebar ul .sidebar-top {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  width: 100%;
  height: 100px;
  padding: 10px 20px;
  overflow: hidden;
  border-bottom: 5px solid var(--second-color);
}

.sidebar ul .sidebar-top img {
  height: 100%;
  width: auto;
  max-width: 100%;
  object-fit: contain;
}

.sidebar ul .sidebar-bottom {
  width: 100%;
  height: calc(100vh - 100px);
  overflow-y: auto;
}

.sidebar ul .sidebar-bottom h5 {
  padding: 20px 0;
  width: 100%;
  text-align: center;
  font-size: 1.2rem;
  font-weight: 600;
  color: var(--light-text);
  background: var(--second-color);
}

.sidebar ul .sidebar-bottom ul li {
  width: 100%;
}

.sidebar ul .sidebar-bottom ul li a {
  display: flex;
  padding: 20px;
  width: 100%;
  color: var(--light-text);
}

.sidebar ul .sidebar-bottom ul li a:hover {
  background: #24346d30;
}

.sidebar ul .sidebar-bottom ul li a.active {
  background: #24346d60;
}

@media screen and (max-width: 768px) {
  .sidebar {
    display: none;
  }

  .sidebar.active {
    display: flex;
  }

  .sidebar .sidebar-button {
    display: flex;
  }
}

.language {
  position: fixed;
  top: 25%;
  right: 0;
  background: linear-gradient(
    90deg,
    var(--second-color) 0%,
    var(--third-color) 100%
  );
  width: 35px;
  height: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  border-radius: 50% 0 0 50%;
  color: #fff;
  font-family: var(--font-bold);
  line-height: 0;
  z-index: 99999;
}

[dir="rtl"] .language {
  right: auto;
  left: 0;
  border-radius: 0 50% 50% 0;
}

.language:hover {
  -webkit-filter: brightness(1.2);
          filter: brightness(1.2);
}



.layout {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  grid-template-rows: 100vh;
  grid-template-areas: "sidebar sidebar sidebar main main main main main main main main main";
  overflow: hidden;
}

.page {
  grid-area: main;
  padding: 50px 20px;
  overflow: auto;
}

/* ! Defaults */

/* Arabic-Fonts */
@font-face {
  font-family: "Tajawal-Bold";
  src: url(/reusable-components/static/media/Tajawal-Bold.76f83be8.ttf);
}

@font-face {
  font-family: "Tajawal-Medium";
  src: url(/reusable-components/static/media/Tajawal-Medium.3358032d.ttf);
}

@font-face {
  font-family: "Tajawal-Regular";
  src: url(/reusable-components/static/media/Tajawal-Regular.e3fe295c.ttf);
}
/* Main Values */
:root {
  --font-bold: "Tajawal-Bold", sans-serif;
  --font-medium: "Tajawal-Medium", sans-serif;
  --font-regular: "Tajawal-Regular", sans-serif;
  --main-color: #101c2f;
  --second-color: #24346d;
  --third-color: #009daa;
  --light-text: #ffffff;
  --dark-text: #212121;
}

/* Scroll Bar */
::-webkit-scrollbar {
  width: 8px;
}

::-webkit-scrollbar-track {
  background: #101c2f;
  background: var(--main-color);
}

::-webkit-scrollbar-thumb {
  background: #a7a7a7;
}

::-webkit-scrollbar-thumb:hover {
  background: #24346d;
  background: var(--second-color);
}

* {
  transition: all 0.1s ease-in-out;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  outline: none;
  text-decoration: none;
}

html {
  scroll-behavior: smooth;
}

body {
  min-height: 100vh;
  font-family: "Tajawal-Regular", sans-serif;
  font-family: var(--font-regular);
  color: #212121;
  color: var(--dark-text);
  cursor: default;
}

h1,
h2,
h3,
h4,
h5,
h6,
p,
span,
a,
button,
input,
textarea,
select,
label,
table,
ul,
li {
  font-size: 1rem;
  font-family: "Tajawal-Regular", sans-serif;
  font-family: var(--font-regular);
  color: #212121;
  color: var(--dark-text);
}

h1 {
  font-size: 2rem;
}

h2 {
  font-size: 1.5rem;
}

h3 {
  font-size: 1.25rem;
}

h4 {
  font-size: 1.125rem;
}

h5 {
  font-size: 1rem;
}

h6 {
  font-size: 0.875rem;
}

a {
  cursor: pointer;
  text-decoration: none;
  color: #212121;
  color: var(--dark-text);
}

input {
  border: none;
  outline: none;
  font-family: "Tajawal-Regular", sans-serif;
  font-family: var(--font-regular);
  color: #212121;
  color: var(--dark-text);
}

button {
  cursor: pointer;
  border: none;
  outline: none;
  background: none;
}

img {
  pointer-events: none;
}

ul {
  padding: 0;
  margin: 0;
  list-style: none;
  display: flex;
}

.container {
  width: 100%;
  max-width: 1200px;
  padding: 0 15px;
  margin: 0 auto;
}

@media (max-width: 1200px) {
  .container {
    max-width: 992px;
  }
}

@media (max-width: 992px) {
  .container {
    max-width: 768px;
  }
}

@media (max-width: 768px) {
  .container {
    max-width: 576px;
  }
}

@media (max-width: 576px) {
  .container {
    max-width: 100%;
  }
}

